@import "../../assets/sass/variables";
@import "../../assets/sass/mixins";


.composition {
  position: relative;

  &__photo {
    width: 55%;
    box-shadow: 0 1.5rem 4rem rgba($color-black, .4);
    border-radius: 2px;
    position: absolute;
    z-index: 1;
    transition: all .2s;
    outline-offset: 2rem; //putting the space between img and the border appeared when hovered

    @include respond(tab-port) {
      position: relative;
      float: left;
      width: 33.33333333%;
      box-shadow: 0 1.5rem 3rem rgba($color-black, .2);
    }

    @include respond(phone) {
      width: 30%;
    }

    &--p1 {
      left: 0;
      top: -2rem;

      @include respond(tab-port) {
        top: 0;
        transform: scale(1.2);
      }
    }
    &--p2 {
      right: 0;
      top: 2rem;

      @include respond(tab-port) {
        top: -1rem;
        transform: scale(1.3);
        z-index: 10;
      }
    }
    &--p3 {
      left: 20%;
      top: 10rem;

      @include respond(tab-port) {
        top: 1rem;
        left: 0;
        transform: scale(1.2);
      }
    }

    &:hover {
      outline: 1rem solid $color-primary;
      z-index: 2;
      transform: scale(1.05) translateY(-.5rem);
      box-shadow: 0 2.5rem 4rem rgba($color-black, .5);

      @include respond(tab-port) {
        outline: .5rem solid $color-primary;
        z-index: 11;
        transform: scale(1.4) translateY(-2rem);
        box-shadow: 0 1.5rem 3rem rgba($color-black, .3);
      }
    }


  }
}

.composition:hover .composition__photo:not(:hover) {
  transform: scale(.95);
}