@import "../../assets/sass/variables";
@import "../../assets/sass/mixins";


.footer {
  background-color: $color-dark-grey-3;
  padding: 10rem 0;
  font-size: 1.4rem;
  color: $color-grey-light-1;

  @include respond(tab-port) {
    padding: 8rem 0;
  }


  &__logo-box {
    text-align: center; //to center img
    margin-bottom: 8rem;

    @include respond(tab-port) {
      margin-bottom: 6rem;
    }

  }
  &__logo {
    width: 15rem;
    height: auto;
  }

  &__navigation {
    border-top: 1px solid $color-dark-grey;
    padding-top: 2rem;
    display: inline-block; //for the border not to overflow the list items

    @include respond(tab-port) {
      width: 100%;
      text-align: center;
    }
  }

  &__list {
    list-style: none;
  }

  &__item {
    display: inline-block;

    &:not(:last-child) {
      margin-right: 1.5rem;
    }
  }

  &__link {
    &:link,
    &:visited {
      background-color: $color-dark-grey-3;
      text-decoration: none;
      text-transform: uppercase;
      color: inherit;
      display: inline-block;
      transition: all .2s;
    }

      &:hover,
      &:active {
        color: $color-primary;
        box-shadow: 0 1rem 2rem rgba($color-black, .4);
        transform: rotate(5deg) scale(1.3);
      }
  }
  &__copyright {
    border-top: 1px solid $color-dark-grey;
    padding-top: 2rem;
    width: 80%;
    float: right;

    @include respond(tab-port) {
      width: 100%;
      float: none;
    }
  }
}